@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;800&display=swap');

*, *:before, *:after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --white: #dfdfdf;
  --white-shade-first:rgba(255, 255, 255, 0.2);
  --black: #000000;
  --black-shade-first: #1c2130;
  --black-shade-second: #333333;
  --black-shade-third: #1f2023;
  --assent-color: #0022CC;
  --assent-color-shade: rgb(0, 23, 138);
  --padding: 10px;
  --margin: 10px;
  --border-radius: 10px;
}

a{
  text-decoration: none;
}

body {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  font-family: 'Poppins', sans-serif;
  background: #1f2023;
  overflow-x: hidden;
}

#root{
  width: 100%;
  min-height: 100vh;
  background: #1f2023;
}