@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;800&display=swap);
*, *:before, *:after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --white: #dfdfdf;
  --white-shade-first:rgba(255, 255, 255, 0.2);
  --black: #000000;
  --black-shade-first: #1c2130;
  --black-shade-second: #333333;
  --black-shade-third: #1f2023;
  --assent-color: #0022CC;
  --assent-color-shade: rgb(0, 23, 138);
  --padding: 10px;
  --margin: 10px;
  --border-radius: 10px;
}

a{
  text-decoration: none;
}

body {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  font-family: 'Poppins', sans-serif;
  background: #1f2023;
  overflow-x: hidden;
}

#root{
  width: 100%;
  min-height: 100vh;
  background: #1f2023;
}
.app {
  width: 100%;
  height: 100%;
  background: var(--black-shade-third);
}

.app-header{
  width: 100%;
  padding: calc(2*var(--padding));
}

.app-title{
  font-weight: 600;
  color: var(--white);
}

.app-sub-title{
  color: var(--white);
}

.app-footer{
  text-align: center;
  font-size: 0.85rem;
}

.app-footer a{
  color: var(--black-shade-second);
}

.container-wraper{
  width: 100%;
  height: calc(100% - 100px);
  min-height: calc(100% - 140px);
  padding: 0px;
  display: flex;
}

.workspace-header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  background: var(--black-shade-first);
  border: 1px solid var(--white-shade-first);
}

.file-name{
  padding: var(--padding);
  border: 1px solid var(--white-shade-first);
  background: var(--black-shade-first);
  color: var(--white);
  border-bottom: 0;
  cursor: pointer;
}

#run{
  cursor: pointer;
  -webkit-appearance: none;
  border: none;
  font-family: 'Poppins', sans-serif;
  padding: calc(0.25*var(--padding)) calc(2*var(--padding));
  background: var(--assent-color);
  color: var(--white);
  font-size: 1.1rem;
  outline: none;
}

#submit-form{
  width:50%;
  height: 100%; 
}

#editor{
  margin: 10px;
}

#output-screen-cotainer{
  width: 50%;
  height: 100%; 
}

#text-editor{
  -webkit-appearance: none;
  font-family: 'Poppins', sans-serif;
  border: 1px solid var(--white-shade-first);
  background: var(--black-shade-first);
  color: var(--white);
  padding: var(--padding);
  height: calc(100vh - 148px);
  min-height: 400px;
  width: 100%;
  font-size: 1rem;
  overflow: auto;
}

#output-screen{
  background: var(--black-shade-first);
  border: 1px solid var(--white-shade-first);
  color: var(--white);
  padding: var(--padding);
  height: calc(100vh - 148px);
  min-height: 400px;
  width: 100%;
  font-size: 1rem;
  overflow: auto;
}


#text-editor::-webkit-scrollbar, #output-screen::-webkit-scrollbar {
  background-color: var(--black-shade-first);
  width:16px
}
#text-editor::-webkit-scrollbar-track, #output-screen::-webkit-scrollbar-track {
  background-color:var(--black-shade-first);
}
#text-editor::-webkit-scrollbar-track:hover, #output-screen::-webkit-scrollbar-track:hover {
  background-color:var(--black-shade-first)
}
#text-editor::-webkit-scrollbar-thumb, #output-screen::-webkit-scrollbar-thumb {
  background-color: var(--white);
  border-radius:16px;
  border:5px solid var(--black-shade-first);
}
#text-editor::-webkit-scrollbar-thumb:hover, #output-screen::-webkit-scrollbar-thumb:hover {
  background-color:var(--white);
  border:4px solid var(--black-shade-first)
}
#text-editor::-webkit-scrollbar-button {
  display:none
}

/* FOCUS */
#text-editor:focus{
  outline:none;
}

#run:hover{
  background: var(--assent-color-shade);
}

/* MEDIA */
@media (max-width: 850px){
  .container-wraper{
    flex-direction: column;
  }
  #submit-form{
    width: 100%;
    height: 100%;
  }
  #output-screen-cotainer{
    width: 100%;
    height: 100%; 
  }
}
